#sidebar-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
}

.sidebar-sticky {
  position: relative;
  top: 0;
  height: 100vh;
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
  font-size: .9rem;
}
#cardcontainer-user {
  position: relative;
}

#cardcontainer-oppo {
  position: relative;
  width: 50%;
  max-width: 125px;
  margin: 0 auto;
  left: -8%;
}

span.w-33 {
  display: inline-block;
  width: 33%;
}

#roundResult-gif {
  position: absolute;
  bottom: 0;
}

.darksalmonred{
  color: #ff6666;
  text-shadow: 6px 6px 0px #ffe6e6
}
.greyshadow{
  text-shadow: 6px 6px 0px #e6e6e6
}

#gameResult {
  white-space: nowrap;
  position: absolute;
  top: 0;
  left: 50%;
  font-size: 5rem;
  font-weight: bold;
  font-family: Courier, cursive;
  text-transform: uppercase;
  -webkit-animation: gameResult-show 800ms cubic-bezier(0.2, 1.15, 0.6, 1.38);
          animation: gameResult-show 800ms cubic-bezier(0.2, 1.15, 0.6, 1.38);
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

#coins-update {
  position: absolute;
  left: 0;
  -webkit-animation: coins-update-show 2s ease-out;
          animation: coins-update-show 2s ease-out;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes coins-update-show {
  from {
    -webkit-transform: translate(0, -1vh);
            transform: translate(0, -1vh);
    opacity: 1;
  }
  to {
    -webkit-transform: translate(0, -6vh);
            transform: translate(0, -6vh);
    opacity: 0;
  }
}

@keyframes coins-update-show {
  from {
    -webkit-transform: translate(0, -1vh);
            transform: translate(0, -1vh);
    opacity: 1;
  }
  to {
    -webkit-transform: translate(0, -6vh);
            transform: translate(0, -6vh);
    opacity: 0;
  }
}

@-webkit-keyframes gameResult-show {
  0% {
    -webkit-transform: translate(0, -5vh);
            transform: translate(0, -5vh);
    opacity: 0;
  }
}

@keyframes gameResult-show {
  0% {
    -webkit-transform: translate(0, -5vh);
            transform: translate(0, -5vh);
    opacity: 0;
  }
}
